/* Menu trái */
.col-aside {
  width: 285px;
  min-width: 180px;
  margin-right: 50px;
  margin-left: 153px;
  padding-top: 12px;
}
.col-aside nav {
  position: sticky;
  margin: 0;
  top: 180px;
}
.col-aside nav ul {
  list-style: none;
  padding: 20px 20px 0;
  margin: 0;
}
.col-aside nav ul li {
  margin-bottom: 24px;
}

.col-aside nav ul li:last-chcild {
  margin-bottom: 0;
}

.col-aside nav ul li a {
  color: #111;
  font-weight: bold;
  font-size: 16px;
  text-decoration: none;
  position: relative;
  padding-left: 32px;
  transition: color 0.2s;
  display: inline-block;
  letter-spacing: -0.5px;
  font-family: "PoppinsExtraBold200", Arial, sans-serif;
}

.col-aside nav ul li.active a {
  color: #2563eb;
}

.col-aside nav ul li.active a::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 28px;
  height: 3px;
  background: #2563eb;
  border-radius: 2px;
}

.col-aside nav ul li a:hover {
  color: #2563eb;
}

