/*Phần đồ thị chart của page3*/
.resume-title {
  color: #3e64ff;
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 30px;
  letter-spacing: 1px;
}

.skills-circular-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  margin-bottom: 40px;
  margin-right: 200px;
}

.skills-circular-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 6px 24px rgba(0, 0, 0, 0.07);
  padding: 32px 20px 24px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 0;
}

.skills-circular-title {
  font-size: 20px;
  font-weight: 700;
  color: #222;
  margin-bottom: 12px;
  letter-spacing: 1px;
}
.skills-circular-chart {
  position: relative;
  width: 170px;
  height: 170px;
  margin-bottom: 15px;
}

.skills-circular-chart svg {
  display: block;
  width: 170px;
  height: 170px;
  transform: rotate(-90deg);
}

.skills-circular-chart .bg {
  fill: none;
  stroke: #e9ecef;
  stroke-width: 10;
}

.skills-circular-chart .progress {
  fill: none;
  stroke: #3e64ff;
  stroke-width: 10;
  stroke-linecap: round;
  transition: stroke-dashoffset 1s cubic-bezier(0.77, 0, 0.18, 1);
}

.skills-circular-percent {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 36px;
  font-weight: 700;
  color: #222;
  line-height: 1;
}
.skills-circular-percent span {
  font-size: 16px;
  font-weight: 400;
  color: #888;
}

.skills-circular-meta {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 10px;
  gap: 10px;
}

.skills-circular-meta-val {
  font-size: 24px;
  font-weight: 700;
  color: #222;
  text-align: center;
}

.skills-circular-meta-label {
  font-size: 12.8px;
  color: #888;
  text-align: center;
  margin-top: 2px;
}

/* Thanh kỹ năng ngang */
.skills-bar-row {
  display: flex;
  gap: 60px;
  justify-content: center;
  margin-top: 30px;
  flex-wrap: wrap;
  margin-right: 200px;
}

.skills-bar-col {
  flex: 1 1 300px;
  min-width: 280px;
  max-width: 420px;
}

.skills-bar-item {
  margin-bottom: 28px;
}

.skills-bar-label {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 5px;
  color: #111;
}

.skills-bar-track {
  width: 400px;
  height: 10px;
  background: #eee;
  border-radius: 2px;
  overflow: hidden;
}

.skills-bar-progress {
  height: 100%;
  background: #3e64ff;
  border-radius: 2px;
  transition: width 0.8s cubic-bezier(0.77, 0, 0.18, 1);
}

/*CSS của cái Service*/
.services-section {
