.blog-card {
  width: 350px;
  background: #ffffff;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: box-shadow 0.3s, transform 0.3s;
  cursor: pointer;
}

.blog-img {
  width: 350px;
  height: 300px;
  background-size: cover;
  background-position: center;
}

.blog-img1 {
  background-image: url("https://images.unsplash.com/photo-1461749280684-dccba630e2f6?auto=format&fit=crop&w=600&q=80");
}
.blog-img2 {
  background-image: url("https://images.unsplash.com/photo-1506744038136-46273834b3fb?auto=format&fit=crop&w=600&q=80");
}
.blog-img3 {
  background-image: url("https://images.unsplash.com/photo-1519125323398-675f0ddb6308?auto=format&fit=crop&w=600&q=80");
}

.blog-content {
  padding: 28px 0px 22px 0px;
  display: flex;
  flex-direction: column;
  flex: 1;
}

.blog-post-title {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 14px;
  color: #222;
  line-height: 1.3;
}
.blog-post-title:hover {
  color: blue;
}

.blog-meta {
  display: flex;
  gap: 15px;
  font-size: 16px;
  color: #3e64ff;
  margin-bottom: 15px;
  font-family: "SemiBold600", Arial, sans-serif;
}

.blog-text {
  color: #999999;
  font-size: 16px;
  margin-bottom: 50px;
  font-family: "Regular400", Arial, sans-serif;
}

/*cái này giống phần Hero nhưng nó ở dưới*/
.freelance-section {
  position: relative;
  text-align: center;
  margin: 60px 0 0 0;
  overflow: hidden;
  z-index: 0;
  height: 417.2px;
}

.freelance-section::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url("../src//assets//image//bg_1.jpg");
  background-size: cover;
  background-position: center;
  opacity: 2;
  filter: blur(1px);
  z-index: 1;
}

.freelance-section::after {
  content: "";
  position: absolute;
  inset: 0;
  background: #3e64ff;
  opacity: 0.75;
  z-index: 2;
  height: 417.2px;
}

.freelance-content {
  position: relative;
  max-width: 700px;
  margin: 0 auto;
  color: #fff;
  z-index: 3;
}

.freelance-title {
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 18px;
  letter-spacing: 1px;
  color: #fff;
}

.freelance-desc {
  font-size: 18px;
  color: #e9ecef;
  margin-bottom: 30px;
  line-height: 1.7;
}

.freelance-btn {
  display: inline-block;
  padding: 13px 38px;
  font-size: 18px;
  font-weight: 600;
  color: #3e64ff;
  background: #fff;
  border-radius: 50px;
  text-decoration: none;
  box-shadow: 0 4px 16px rgba(62, 100, 255, 0.1);
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  margin-top: 10px;
  border: none;
  cursor: pointer;
}

.freelance-btn:hover {
  background: #3e64ff;
  color: #fff;
  box-shadow: 0 8px 24px rgba(62, 100, 255, 0.18);
  border: 1px solid #fff;
}

/*Phần này dành cho contact*/
.contact-section {
  background: #f8f9fa;
  padding: 70px 0 60px 0;
}

.contact-header {
  text-align: center;
  margin-bottom: 40px;
}

.contact-title {
  font-size: 38px;
  font-weight: 700;
  margin-bottom: 10px;
  color: #222;
}

.contact-desc {
  color: #888;
  font-size: 18px;
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 35px;
  max-width: 800px;
  margin: 0 auto;
}

.contact-card {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.07);
  padding: 36px 28px 30px 28px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.contact-label {
  font-size: 17px;
  font-weight: 700;
  color: #3e64ff;
  margin-bottom: 10px;
  letter-spacing: 1px;
}

.contact-info {
  font-size: 16px;
  color: #222;
  word-break: break-word;
}
